<template>
    <div>
        <nav-bar :name="title"></nav-bar>

        <v-card class="ma-1" cols="12">
        <v-container>
        <v-row>
            <v-col cols="12">
            <v-text-field
            class="mb-n5"
            outlined
            dense
            required
            v-model= "store.name"
            label= "Name"
            ></v-text-field>
            </v-col>
            <v-col cols="12">
            <v-text-field
            class="mb-n5"
            outlined
            dense
            v-model= "store.phone_number"
            label= "Phone number"
            ></v-text-field>
            </v-col>
            <v-col cols="12">
            <v-text-field
            class="mb-n5"
            outlined
            dense
            v-model= "store.email"
            label= "Email"
            ></v-text-field>
            </v-col>
            <v-col cols="12">
            <v-text-field
            class="mb-n5"
            outlined
            dense
            v-model= "store.address"
            label= "Address"
            ></v-text-field>
            </v-col>
            <v-col cols="12">
            <v-text-field
            class="mb-n5"
            outlined
            dense
            type="number"
            v-model= "store.zipcode"
            label= "Zip code"
            ></v-text-field>
            </v-col>
            <v-col cols="12">
            <v-text-field
            class="mb-n5"
            outlined
            dense
            v-model= "store.state"
            label= "State"
            ></v-text-field>
            </v-col>
            <v-col cols="12">
            <v-select
            class="mb-n5"
            :items="items"
            label="Status"
            outlined
            dense
            v-model= "store.status"
            ></v-select>
            </v-col>
            <v-col>
            <v-btn
            class="mx-4 my-2"
            color="#989898"
            dark
            @click="$router.go(-1)"
            >
            GO BACK
            </v-btn>
            <v-btn
            class="mx-4 my-2"
            color="#E47911"
            dark
            :loading="loading"
            @click="update"
            >
            UPDATE
            </v-btn>
            </v-col>
        </v-row>
        </v-container>
        </v-card>
    </div>
</template>

<script>
    import NavBar from '@/components/NavBar';
    import StoreService from '@/services/StoreService.js';
    import Swal from 'sweetalert2'

    export default {
        components: {
        NavBar
        },

        data: () => ({
            title: 'UPDATE STORE',
            loading: false,
            store: {
                name: "",
                email: "",
                phone_number: "",
                address: "",
                zipcode: null,
                state: "",
                status: "",
            },
            items: [
            { text: 'Active', value: "A" },
            { text: 'Pending activation', value: "PA" },
            { text: 'Suspended', value: "S" },
            { text: 'Denied activation', value: "DA" },
            { text: 'Vacation', value: "V" },
            { text: 'Everyday', value: "ED" },
            ]
        }),

        computed:{
            unique_id(){
                return this.$route.params.id
            }
        },

        mounted(){
            StoreService
                .get_info(this.unique_id)
                .then(res => {
                    this.store.name = res.name;
                    this.store.phone_number = res.phone_number;
                    this.store.email = res.email;
                    this.store.address = res.address;
                    this.store.zipcode = res.zipcode;
                    this.store.state = res.state;
                    this.store.status = res.status;
                })
                .catch(e => {
                    let error = e.response.data;
                    let msg = ''
                    Object.entries(error).forEach(([ key, value ]) => {
                        msg+= key + ': ' + value + ' \n ';
                    });
                        this.loading = false;
                        Swal.fire({
                        icon: 'error',
                        title: 'Error',
                        text: msg
                        });
                    })
        },

        methods:{
            update(){
                this.loading = true;
                StoreService
                    .update(this.store, this.unique_id)
                    .then(res => {
                        this.loading = false;
                        Swal.fire({
                        icon: 'success',
                        title: 'Success',
                        text: 'The store was successfully modified.'
                        }).then(() => {
                            this.$router.go(-1)
                        })
                    })
                    .catch(e => {
                    let error = e.response.data;
                    let msg = ''
                    Object.entries(error).forEach(([ key, value ]) => {
                        msg+= key + ': ' + value + ' \n ';
                    });
                        this.loading = false;
                        Swal.fire({
                        icon: 'error',
                        title: 'Error',
                        text: msg
                        });
                    })
            }
        },

    }
</script>