<template>
  <div>
    <v-toolbar
    dense
    color="#000"
    dark>
    <v-btn icon>
    <v-app-bar-nav-icon @click.stop="drawer = !drawer"></v-app-bar-nav-icon>
    </v-btn>
    <v-toolbar-title>{{ name }}</v-toolbar-title>
    <v-spacer></v-spacer>
    <v-btn icon @click="logout()">
    <v-icon> mdi-logout </v-icon>
    </v-btn>
    </v-toolbar>

    <v-navigation-drawer
    v-model="drawer"
    absolute
    temporary
    color="#000"
    dark
    >
    <v-list nav dense>
        <v-list-item-group
        v-model="group"
        active-class="gray--text text--accent-4"
        dark
        >
        <router-link to="/dashboard">
        <v-list-item>
            <v-icon> mdi-home </v-icon> <v-list-item-title> HOME </v-list-item-title>
        </v-list-item>
        </router-link>

        <router-link to="/contact">
        <v-list-item>
            <v-icon> mdi-phone </v-icon> <v-list-item-title> CONTACT </v-list-item-title>
        </v-list-item>
        </router-link>

        <router-link to="/faqs">
        <v-list-item>
            <v-icon> mdi-home </v-icon> <v-list-item-title> FAQ'S </v-list-item-title>
        </v-list-item>
        </router-link>

        <router-link to="/profile">
        <v-list-item>
            <v-icon> mdi-account </v-icon> <v-list-item-title> PROFILE </v-list-item-title>
        </v-list-item>
        </router-link>
        </v-list-item-group>
    </v-list>
    </v-navigation-drawer>
  </div>
</template>

<script>
export default {
    name: 'NavBar',
    props: ['name'],

    data: () => ({
      drawer: false,
      group: null,
    }),
    computed: {

      /* ESTE ES UN METODO QUE SIEMPRE ESTA ACTIVO, 
          QUE RETORNA EL ESTATUS DEL USUARIO */
      loggedIn() {
          return this.$store.state.auth.status.loggedIn;
      }
  },

    created() {
      /* SI EL USUARIO ESTA ACTIVO LO ENVIA A LA PANTALLA DE DASHBOARD */
      if (!this.loggedIn){
          this.$router.push('/');
      }
    },

    methods: {
      logout (){
        this.$store.dispatch('auth/logout')
      }
    },

    watch: {
      group () {
        this.drawer = false
      },
    }
}
</script>

<style>
.v-application a {
     text-decoration: none;
}
</style>