import http from '@/assets/http-commom.js'
import Swal from 'sweetalert2'

class StoreService {
    list_admin(){
        return http
            .get('admin/stores/')
            .then(res => {
                return res.data
            })
    }

    list_customer(){
        return http
            .get('stores_owner/')
            .then(res => {
                return res.data
            })
    }

    stores_owned(pk){
        return http
        .get('stores_owner/'+pk+'/stores/')
        .then(res => {
            return res.data
        })
    }

    add_store_customer(data){
        return http
            .post('stores_owner/', {
                name: data.name,
                email: data.email,
                phone_number: data.phone_number,
                address: data.address,
                zipcode: data.zipcode,
                state: data.state,
                status: data.status
            })
            .then(res => {
                return res.data
            })
    }

    add_store_admin(data){
        return http
            .post('admin/stores/', {
                name: data.name,
                email: data.email,
                phone_number: data.phone_number,
                address: data.address,
                zipcode: data.zipcode,
                state: data.state,
                status: data.status
            })
            .then(res => {
                return res.data
            })
    }

    add_owner(data, pk){
        return http
            .post('admin/stores/'+pk+'/add_owner/', {
                user: data
            })
            .then(res => {
                return res.data
            })
    }

    remove_owner(pk){
        return http
            .delete('stores_owner/'+pk+'/')
            .then(res => {
                return res.data
            })
    }

    update(data, pk){
        return http
            .put('stores_owner/'+pk+'/', {
                name: data.name,
                email: data.email,
                phone_number: data.phone_number,
                address: data.address,
                zipcode: data.zipcode,
                state: data.state,
                status: data.status
            })
            .then(res => {
                return res.data
            })
    }

    delete(pk){
        return http
            .delete('admin/stores/'+pk+'/')
            .then(res => {
                return res.data
            })
    }

    get_info(pk){
        return http
            .get('stores_owner/' + pk + '/')
            .then(res => {
                return res.data
            })
    }

    get_walmart(pk){
        return http
            .get('walmart/' + pk + '/store/')
            .then(res => {
                return res.data
            })
    }
    
    list_owners(pk){
        return http
            .get('stores_owner/'+ pk +'/owners/')
            .then(res => {
                return res.data
            })
    }

    delete_stores(data){
        return http
            .post('admin/stores/remove_stores/', {
                id_stores: data
            })
            .then(res => {
                return res.data
            })
    }

    export_stores(data){
        return http
            .post('admin/stores/export_stores/', {
                id_stores: data
            }, {responseType: 'blob'})
            .then(res => {
                return res
            })
    }

    add_walmart(data){
        return http
            .post('walmart/', {
                store: data.store,
                walmart_username: data.walmart_username,
                walmart_password: data.walmart_password,
                last_4_digits_of_the_card: data.card,
                CVV: parseInt(data.CVV),
                month: data.month,
                year: data.year
            })
            .then(res => {
                return res.data
            })
    }

    update_walmart(data){
        return http
            .put('walmart/'+data.id+'/', {
                walmart_username: data.username,
                walmart_password: data.password,
                last_4_digits_of_the_card: data.card,
                CVV: parseInt(data.CVV),
                month: data.month,
                year: data.year
            })
            .then(res => {
                return res.data
            })
    }

    send_email(data){
        return http
        .post('admin/stores/send_email/', {
            subject: data.subject,
            message: data.message,
            emails: data.emails,
        })
        .then( res => {
            return res.data
        })
    }

    send_request_info(data){
        return http
        .post('admin/stores/send_information/', {
            emails: data.emails,
            message: data.message,
        })
        .then( res => {
            return res.data
        })
    }
}

export default new StoreService();